body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.contact{margin-top:200px;width:100%;height:200px;background-color:white;display:flex;justify-content:center;align-items:center;flex-direction:column}.contact p{font-style:normal;line-height:2rem;padding-bottom:100px}

.section{height:100vh;display:flex;justify-content:center;align-items:center}@media only screen and (max-width: 1024px){.section{height:100%}}.scrollable-desktop{position:absolute;width:100%;height:80%;display:flex;overflow:auto;flex-direction:row;justify-content:space-between}@media only screen and (max-width: 1024px){.scrollable-desktop{display:none}}.scrollable-desktop .left{width:50%;height:100%}.scrollable-desktop .left h1{letter-spacing:3px;margin:0;padding-left:10px;padding-top:10px;color:#fff;font-weight:100}.scrollable-desktop .left .content{width:100%;height:100%}.scrollable-desktop .left .content .text{width:100%;height:550px;position:-webkit-sticky;position:sticky;top:30%}.scrollable-desktop .left .text{display:flex;justify-content:center;align-items:flex-start}.scrollable-desktop .left .text h1{color:#000}.scrollable-desktop .left .text p{font-style:normal;width:60%;text-align:left;padding-bottom:20px;border-bottom:2px solid #d43e22}.scrollable-desktop .phone{width:30%;margin-right:250px;position:-webkit-sticky;position:sticky;top:5%;background-size:contain;background-repeat:no-repeat;background-position:center;display:flex;justify-content:center;z-index:3}.scrollable-desktop .screen1{object-fit:contain;width:90%;height:90%;-webkit-transition:opacity 1.5s ease-out;transition:opacity 1.5s ease-out}.scrollable-desktop .screen2{object-fit:contain;width:90%;height:90%;position:absolute;-webkit-transition:opacity 1.5s ease-out;transition:opacity 1.5s ease-out}.scrollable-desktop .screen3{object-fit:contain;width:90%;height:90%;position:absolute;-webkit-transition:opacity 1.5s ease-out;transition:opacity 1.5s ease-out}.scrollable-desktop .screen4{object-fit:contain;width:90%;height:90%;position:absolute;-webkit-transition:opacity 1.5s ease-out;transition:opacity 1.5s ease-out}.scrollable-mobile{width:100%;display:flex;flex-direction:column;justify-content:flex-start;margin-top:280px;align-items:center}@media only screen and (min-width: 1025px){.scrollable-mobile{display:none}}@media only screen and (max-width: 414px){.scrollable-mobile{margin-top:120px}}.scrollable-mobile .phone{width:30%;background-size:contain;background-repeat:no-repeat;background-position:center;display:flex;justify-content:center;z-index:3}@media only screen and (max-width: 414px){.scrollable-mobile .phone{width:80%}}.scrollable-mobile .screen1{object-fit:contain;width:90%;height:90%;-webkit-transition:opacity 1.5s ease-out;transition:opacity 1.5s ease-out}.scrollable-mobile .text{display:flex;justify-content:center;align-items:flex-start;margin-top:40px;margin-bottom:80px}.scrollable-mobile .text h1{color:#000}.scrollable-mobile .text p{font-style:normal;width:50%;text-align:center;padding-bottom:20px;border-bottom:2px solid #d43e22}@media only screen and (max-width: 414px){.scrollable-mobile .text p{width:80%;font-size:16px}}

.features{width:100%;height:500px;background-color:#d43e22;display:flex;justify-content:center;align-items:center}@media only screen and (max-width: 1024px){.features{height:100%}}.features .box-desktop{width:90%;height:90%;display:flex;flex-direction:row;justify-content:space-between;align-items:center}@media only screen and (max-width: 1024px){.features .box-desktop{flex-direction:column}}.features .box-desktop .description{text-align:left;width:20%;display:flex;flex-direction:column;justify-content:center;align-items:center}@media only screen and (max-width: 1024px){.features .box-desktop .description{width:40%;text-align:center;margin-top:50px}}@media only screen and (max-width: 414px){.features .box-desktop .description{width:70%}}.features .box-desktop .description .text{height:120px}@media only screen and (max-width: 1024px){.features .box-desktop .description .text{margin-top:50px}}.features .box-desktop .description .img-container{width:250px;height:250px;border-bottom:1px white dashed}.features .box-desktop .description .featureImg{width:250px;height:250px;object-fit:cover}.features .box-desktop .description p{font-style:normal;color:white}.features .box-desktop .description #premium{font-size:12px;font-style:italic}

